<template>
    <div class="ccode">
        <div class="h-headtitle">常用javascript方法</div>
        <div class="h-blocks">
            <div class="h-title">精确计算加减乘除</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            /**
                             * 加法
                             * @param arg1
                             * @param arg2
                            */
                            function addition(arg1,arg2){
                              let r1,r2,m;
                              try{r1=arg1.toString().split(".")[1].length}catch(e){r1=0};
                              try{r2=arg2.toString().split(".")[1].length}catch(e){r2=0};
                              m=Math.pow(10,Math.max(r1,r2));
                              return (arg1*m+arg2*m)/m;
                            }

                            /**
                            * 减法
                            * @param arg1
                            * @param arg2
                            */
                            function subtraction(arg1,arg2){
                              let r1,r2,m,n;
                              try{r1=arg1.toString().split(".")[1].length;}catch(e){r1=0;}
                              try{r2=arg2.toString().split(".")[1].length;}catch(e){r2=0;}
                              m=Math.pow(10,Math.max(r1,r2));
                              //动态控制精度长度
                              n=(r1>=r2)?r1:r2;
                              return ((arg1*m-arg2*m)/m).toFixed(n);
                            }
                            /***
                            * 乘法，获取精确乘法的结果值
                            * @param arg1
                            * @param arg2
                            */
                            function multiplication(arg1,arg2){
                              let m=0,s1=arg1.toString(),s2=arg2.toString();
                              try{m+=s1.split(".")[1].length}catch(e){};
                              try{m+=s2.split(".")[1].length}catch(e){};
                              return Number(s1.replace(".",""))*Number(s2.replace(".",""))/Math.pow(10,m);
                            }
                            /***
                            * 除法，获取精确乘法的结果值
                            * @param arg1
                            * @param arg2
                            */
                            function division(arg1,arg2){
                              let t1=0,t2=0,r1,r2;
                              try{t1=arg1.toString().split(".")[1].length}catch(e){}
                              try{t2=arg2.toString().split(".")[1].length}catch(e){}
                              r1=Number(arg1.toString().replace(".",""));
                              r2=Number(arg2.toString().replace(".",""));
                              return (r1/r2)*Math.pow(10,t2-t1);
                            }
                        </code>
                    </pre>
                </div>
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">url参数格式化</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            /*url参数格式化
                            * 如：www.xxx.com?id=1&age=20
                            * 返回：{id:1,age:20}
                            * */
                            paramsFormat(url) {
                                let qInd = url.indexOf("?");
                                let sharpInd = url.indexOf("#"); //路由
                                let search = "";
                                let paramsList = [];
                                let paramsObj = {};
                                if (qInd >= 0) {
                                    if (sharpInd > 0) {
                                        search = url.substring(qInd + 1, sharpInd);
                                    } else {
                                        search = url.substring(qInd + 1);
                                    }
                                    //from跳转链接，不需要解析其他参数
                                    let reg = /\?(from(?:url)?)=(.*\.html)/i;
                                    if (reg.test(search)) {
                                        let matches = search.match(reg);
                                        paramsObj[matches[1]] = matches[2];
                                    } else {
                                        paramsList = search.split("&");
                                        for (var ind = 0; ind < paramsList.length; ind++) {
                                            var param = paramsList[ind];
                                            if (param) {
                                                var pind = param.indexOf("=");
                                                if (pind >= 0) {
                                                    let key = param.substring(0, pind);
                                                    let value = param.substr(pind + 1);
                                                    if (value == 'undefined' || value == 'null') value = null;//过滤字符串，否则接口报错
                                                    paramsObj[key] = value;
                                                } else {
                                                    paramsObj[param] = "";
                                                }
                                            }
                                        }
                                    }
                                }
                                return paramsObj;
                            }
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">对象转成url地址格式</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            /*对象转成url地址格式
                            * 如：{id:1,age:20}
                            * 返回：www.xxx.com?id=1&age=20
                            * */
                            jsonToParamsFormat(d) {
                                let urls = '';
                                if (!d) {
                                    return;
                                }
                                for (let key in d) {
                                    urls += `&${key}=${d[key]}`
                                }
                                let nUrls = '?' + urls.slice(1);  //去掉第一个&符号
                                return nUrls;
                            }
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">对url里的参数删除指定某个参数</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            /*
                            * 如www.xxx.com/index?id=1&age=20，删除age
                            * 返回 www.xxx.com?id=1
                            *
                            * url,字符串
                            * paramArr 数组
                            * */
                            getNewUrls(url, paramArr) {
                                let rPath = '';
                                if (paramArr.length <= 0) {
                                    return;
                                }
                                let getParamArr = this.paramsFormat(url);   //获取url所以对象数据
                                let getPathSliceIndex = url.indexOf('?');   //获取？的序号
                                let getPathSliceTxt = url.slice(0, getPathSliceIndex);
                                for (let i = 0; i < paramArr.length; i++) {
                                    delete getParamArr[paramArr[i]]
                                }
                                let paramUrlTxt = this.jsonToParamsFormat(getParamArr);
                                rPath = getPathSliceTxt + paramUrlTxt
                                return rPath;

                            }
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">实现将本地资源图片转化成base64</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            function convertImgToBase64(url, callback, outputFormat) {
                                let canvas = document.createElement('CANVAS'),
                                    ctx = canvas.getContext('2d'),
                                    img = new Image;
                                img.crossOrigin = 'Anonymous';
                                img.onload = function () {
                                    canvas.height = img.height;
                                    canvas.width = img.width;
                                    ctx.drawImage(img, 0, 0);
                                    var dataURL = canvas.toDataURL(outputFormat || 'image/png');
                                    callback.call(this, dataURL);
                                    canvas = null;
                                };
                                //console.log(url);
                                img.src = url;
                            },
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">日期时间转时间戳</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            //时间--时间戳 2012-01-12 12:12:12  to  1514514514515
                            time2timestamp(dateTime) {
                                if (dateTime) {
                                    return new Date(dateTime.replace(/-/g, '/')).getTime();
                                }
                            },
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">时间戳转换成时间</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            function timestamp2Time(stamp) {
                                if (stamp) {
                                    let date = new Date(stamp);
                                    let y = date.getFullYear();
                                    let m = (date.getMonth() + 1) < 10 ? "0" + (date.getMonth() + 1) : (date.getMonth() + 1);
                                    let d = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
                                    let hour = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
                                    let mins = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
                                    let seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
                                    return y + "-" + m + '-' + d + ' ' + hour + ':' + mins + ':' + seconds;
                                }
                            },
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">时间对象转换成时间</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            //new Date() to  2012-01-12 12:12:12
                            function timeObj2Time(stamp) {
                                if (stamp) {
                                    let dObj = stamp.getTime();
                                    let date = new Date(dObj);
                                    let y = date.getFullYear();
                                    let m = (date.getMonth() + 1) < 10 ? "0" + (date.getMonth() + 1) : (date.getMonth() + 1);
                                    let d = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
                                    let hour = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
                                    let mins = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
                                    let seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
                                    return y + "-" + m + '-' + d + ' ' + hour + ':' + mins + ':' + seconds;
                                }
                            },
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">判断小程序环境</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            isWeChatMiniApp() {
                                let ua = window.navigator.userAgent.toLowerCase();
                                return new Promise((resolve) => {
                                    if (ua.indexOf('micromessenger') == -1) {
                                        //console.log("不在微信或者小程序中")
                                        resolve(false);
                                    } else {
                                        wx.miniProgram.getEnv((res) => {
                                            if (res.miniprogram) {
                                                //console.log("在小程序中")
                                                resolve(true);
                                            } else {//在微信中
                                                //console.log("在微信中")
                                                resolve(false);
                                            }
                                        });
                                    }
                                });
                            },
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">递归算法-遍历</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                            let arr = [
                                    {
                                        id:'1',
                                        name:'n1',
                                        children:[
                                            {
                                                id:'1-1',
                                                name:'n1-1',
                                                children:[]
                                            },
                                            {
                                                id:'1-2',
                                                name:'n1-2',
                                                children:[]
                                            }
                                        ]
                                    },
                                    {
                                        id:'2',
                                        name:'n2',
                                        children:[
                                            {
                                                id:'2-1',
                                                name:'n2-1',
                                                children:[
                                                    {
                                                        id:'2-1-1',
                                                        name:'n2-1-1',
                                                        children:[]
                                                    }
                                                ]
                                            },
                                        ]
                                    },
                                    {
                                        id:'3',
                                        name:'n3',
                                        children:[]
                                    }
                                ];

                                let getArray = (data,id)=>{
                                    for (let i in data) {
                                        if (data[i].id == id) {
                                            //console.log('找到了');
                                            break;
                                        } else {
                                            //console.log('继续找');
                                            getArray(data[i].children, id);
                                        }
                                    }
                                };
                                getArray(arr,'2-1');
                        </code>
                    </pre>
                </div>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">获取视频第一帧图片</div>
            <div class="h-main martop0">
                <div v-highlight>
                    <pre>
                        <code>
                        chooseVideoGetFirstFrameImg(e,obj) {
                            let self = this;
                            //逻辑判断，防止少传
                            if(Object.keys(arguments[1]).length < 3){
                                console.log('缺少参数');
                                return;
                            }
                            if(!obj.videoDivId){
                                console.log('请传videoDivId');
                                return;
                            }
                            if(!obj.picWidth){
                                console.log('请传picWidth');
                                return;
                            }
                            if(!obj.picHei){
                                console.log('请传picHei');
                                return;
                            }
                            let files = e.target.files[0];//获取文件
                            let blob = new Blob([files]); // 文件转化成二进制文件
                            let url = URL.createObjectURL(blob); //转化成url
                            let parent = document.getElementById(obj.videoDivId);
                            let div = document.createElement("div");
                            div.setAttribute("class", "newDivVideo");
                            parent.appendChild(div);
                            document.querySelector('.newDivVideo').innerHTML = '&lt;video controls src="'+url+'" id="videoId" width="100%" height="100%" autoplay&gt;&lt;/video&gt;';
                            let videoElement = document.querySelector('#videoId');
                            videoElement.addEventListener("canplay", function() {
                                let canvas = document.createElement("canvas");
                                canvas.width = obj.picWidth;
                                canvas.height = obj.picHei;
                                canvas.getContext("2d").drawImage(videoElement, 0, 0, canvas.width, canvas.height);
                                let outImgUrl = canvas.toDataURL("image/png");
                                console.log(outImgUrl); //第一帧图片url
                            })
                        }
                        </code>
                    </pre>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'common-code',
        data() {
            return {

            }
        },
        mounted(){
            let arr = [
                {
                    id:'1',
                    name:'n1',
                    children:[
                        {
                            id:'1-1',
                            name:'n1-1',
                            children:[]
                        },
                        {
                            id:'1-2',
                            name:'n1-2',
                            children:[]
                        }
                    ]
                },
                {
                    id:'2',
                    name:'n2',
                    children:[
                        {
                            id:'2-1',
                            name:'n2-1',
                            children:[
                                {
                                    id:'2-1-1',
                                    name:'n2-1-1',
                                    children:[]
                                }
                            ]
                        },
                    ]
                },
                {
                    id:'3',
                    name:'n3',
                    children:[]
                }
            ];

            let getArray = (data,id)=>{
                for (let i in data) {
                    if (data[i].id == id) {
                        //console.log('找到了');
                        break;
                    } else {
                        //console.log('继续找');
                        getArray(data[i].children, id);
                    }
                }
            };
            getArray(arr,'2-1');
        },
        methods:{

        },
        components:{

        }
    }
</script>
<style scoped>
    .martop0{margin-top:0px;}
</style>



